#navigation {
  margin-right: 15px;
  padding: 0;
  align-self: center;

}
#navigation ul  {
    margin: 0
}

#navigation ul li {
  display: inline-block;
  list-style-type: none;
  margin: 0 0 0 30px;
}

#navigation ul li a {
  text-decoration: none;
  color: #A1D6E2;
  transition: 0.5s;
}

#navigation ul li a:hover {
  color: #A1D6E2;
  cursor: pointer;
}

#navigation ul li a .active {
  color: #A1D6E2;
  font-size: 20px;
}

